.App {
  text-align: center;
  overflow-y: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Promotional Content CSS */
#banner {
  position: absolute;
  width: 100vw;
  height: 100vh;
  display: none;
  top: 0%;
  background-color: rgba(255, 255, 255, 0.5);
}

#banner-img {
  width: 90vw;
  height: 95vh;
  margin-top: 2.5vh;
  border-radius: 2%;
}

#close-btn {
  position: absolute;
  top: 4vh;
  right: 6vw;
}

#action-btn {
  position: absolute;
  bottom: 35vh;
  right: 10vw;
}
